.container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
h1{
  height: fit-content;
}
.move{
text-align: center;
width: 400px;
margin-left: 40%;
color: rgb(255, 0, 0);
text-shadow:2px 2px 5px rgb(73, 71, 71);
}
.img-c{
  width: 200px;
  height: 120px;
}
.card1{
  margin: 20px;
  border-radius: 10px;
  text-align: center;
  border: 1px solid black;
  width: 440px;
  cursor: pointer;
  height: fit-content;
  background-color: rgb(153, 192, 238);
}
a{
  color: rgb(41, 25, 6);
}
footer{
  font-size: 1em;
  margin-left: 50%;
}
.countrie{
  font-style: italic;
  height: fit-content;
  color: darkorange;
}
.card1:hover{
  padding: 2px;
  background-color: rgb(251, 255, 0);
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
